.mainBn {
    height: 45vh;
    color: $white;
    padding: 4%;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-start;

    h1 {
        font-weight: bold;
        font-size: 20px;
    }

    p {
        font-size: 11px;
    }

    &.index_bn {
        background: url(../images/index_bn.jpg) no-repeat center bottom;
        background-size: cover;
    }

    &.receipt_bn {
        background: url(../images/receipt_bn.jpg) no-repeat center bottom;
        background-size: cover;
    }

    &.payment_bn {
        background: url(../images/payment_bn.jpg) no-repeat center bottom;
        background-size: cover;
    }

    @include media-breakpoint-only(sm) {
        //pad
        height: 90vh;

        h1 {
            font-weight: bold;
            font-size: 25px;
        }

        p {
            font-size: 1rem;
        }
    }

    @include media-breakpoint-only(md) {
        //pad
        height: 60vw;

        h1 {
            font-size: 30px;
        }

        p {
            width: 650px;
            font-size: 1rem;
        }
    }

    @include media-breakpoint-only(lg) {
        //pad pro
        height: 65vw;

        h1 {
            font-size: 40px;
        }

