header {
    width: 100%;
    position: fixed;
    top: 0;
    background: $white;
    z-index: 555;

    .logo {
        width: 100px;
        margin: .5rem auto;

        img {
            width: 100%;
        }
    }

    // .logo {
    //     margin: .5rem 1rem;

    //     a {
    //         width: 130px;
    //         display: inline-block;
    //         padding-bottom: .5rem;

    //         img {
    //             width: 100%;
    //             vertical-align: bottom;
    //         }
    //     }
    // }
    &#headerArea {
        .logo {
            margin-left: 1rem;
        }
    }

    .nav_icon {
        img {
            width: 28px;
            height: 28px;
            margin-right: .5em;
        }
    }

    @include media-breakpoint-up(md) {
        padding-top: 10px;

        .logo {
            width: 120px;
            margin-left: 10px;
        }

    }


    &.header_transparent {
        position: absolute;
        top: 0;
        background: transparent;
    }

    &.header_blue {
        background: $primary;
        @include shadow(1px, 3px);
    }

    &.header_purple {
        background: $purple;
        @include shadow(1px, 3px);
    }

    a.nav-link {
        position: relative;
        text-align: center;
        margin: 0 .2rem;
        color: $white;

        &:before {
            content: "";
            position: absolute;
            bottom: 3px;
            z-index: -1;
            background: $white;
            left: 51%;
            right: 51%;
            height: 3px;
            @include tran(0.2s, ease);
        }

        &:hover:before {
            left: 15%;
            right: 15%;
        }

        &.active {
            font-weight: bold;

            &:before {
                content: "";
                position: absolute;
                bottom: 3px;
                z-index: -1;
                background: $white;
                left: 15%;
                right: 15%;
                height: 3px;
            }
        }
    }

    @include media-breakpoint-up(lg) {
        position: relative; // height: 70px;
        z-index: 99;
        top: 0;

        &.headerShow {
            position: fixed;
            top: 0;

            @include keyframes(headerMove) {
                0% {
                    opacity: 0;
                    top: -5px;
                }

                90% {
                    opacity: 1;
                    top: 0;
                }
            }

            @include animation('headerMove .2s');
        }
    }

    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .nav-link {
            font-size: 20px !important;
        }

        .dropdown-item {
            font-size: 16px !important;

        }
    }

    .navbar {
        padding: 0;
    }



    .link_register_lg {
        display: none;
    }



    @include media-breakpoint-down(md) {

        //phone
        .nav_main {
            width: 100%;
            left: 0;

            a.nav-link {
                position: relative;
                padding: .5rem .5rem;
                margin: 0 .5rem;
                color: $primary !important;
                font-size: 1rem;
                text-align: left;
                // border: 1px solid #f00;
                font-weight: bold;
            }

            .nav_main_in {
                a {
                    color: $primary;
                    font-size: 16px;
                }
            }

            .navbar-toggler {
                right: 0;
                margin-top: -.45rem;
                margin-right: 3%;
            }


        }

        .nav_right {
            a.nav-link {
                display: block;
                text-align: center;
                background: $primary;
                @include border-radius($border-radius);
            }
        }


        // @include media-breakpoint-only(md) {


        //     .navbar-toggler {
        //         margin-top: -1.4rem;
        //     }

        //     .nav_main {
        //         margin-left: 0;
        //         padding-left: 5%;
        //     }
        // }
    }

    @include media-breakpoint-up(md) {
        .nav_main {}
    }

    @include media-breakpoint-up(lg) {
        //PC
        padding: 0 2rem;

        .nav_right {
            position: absolute;
            right: 1rem;
            display: flex;

            a.nav-link {
                color: $white;
            }
        }

        .navbar-collapse {
            text-align: center;
        }

        .nav_main {
            margin-left: 8%;

            .nav-item {
                position: relative;
                // margin-right: 3rem;
                // padding: .7rem 0;
            }


            .nav_main_in {
                position: absolute;
                margin-top: 6px;
                margin-left: -60%;
                padding: .8em 0;
                background: $white;
                @include border-radius($border-radius);
                @include shadow(0px, 5px);
                display: none;
                opacity: 0;

                &:after {
                    content: "";
                    position: absolute;
                    top: -5px;
                    left: 45%;
                    text-align: center;
                    @include triangle(top, $white, 6px, 16px);
                }
            }

            &>.nav-item:hover>.nav_main_in {
                @include tran(0.2s, ease);
                opacity: 1;
                display: block;

                .dropdown-item {
                    padding: .45em 3em;
                    font-size: 18px;
                    text-align: left;

                    &:hover {
                        color: darken($primary, 20%);
                        background: lighten($gray-300, 10%);
                    }
                }
            }
        }
    }

    @include media-breakpoint-up(xl) {}

    &.logo_center {
        display: flex;
        justify-content: center;
        align-content: center;

        .logo {
            text-align: center;
            padding-bottom: 0;
            margin: .7rem 0;

            a {
                width: auto;
                align-self: center;
            }

            img {
                height: 40px;
                width: auto;

                @include media-breakpoint-up(md) {
                    height: 150px;
                }
            }
        }

        b {
            display: inline-block;
            align-self: center;
            margin-top: -1rem;
            font-size: 30px;
            height: 40px;
            ;
            color: $blue_dark;
        }
    }

    .headArea {
        text-align: center;
    }

    .mainBn {
        width: 100%;
        height: 100vh;
        background-image: url('../images/main_bn.jpg');
    }
}

