.bg_gradient {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    height: 350px;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#6a5cff+0,42a6fd+32,32c3fc+43,30c7fc+100 */
    background: #6a5cff;
    /* Old browsers */
    background: -moz-linear-gradient(45deg, #6a5cff 0%, #42a6fd 32%, #32c3fc 43%, #30c7fc 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg, #6a5cff 0%, #42a6fd 32%, #32c3fc 43%, #30c7fc 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(45deg, #6a5cff 0%, #42a6fd 32%, #32c3fc 43%, #30c7fc 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#6a5cff', endColorstr='#30c7fc', GradientType=1);
    /* IE6-9 fallback on horizontal gradient */
    @include skew(0, -12);
    @include transform-origin (top left);

    @include media-breakpoint-up(md) {
        height: 600px;

    }

}


