ul.stepBar {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 150px;
    margin: 0;
    padding: 0;


    &>li {
        width: 25%;
        text-align: center;
        position: relative;
        padding-top: 5px;
        list-style: none;

        .step_in {
            width: 100%;
            position: relative;
            z-index: 5;
            text-align: center;
            border-right: 1px solid $white;
        }

        .step_ball {
            width: 40px;
            height: 40px;
            padding: 3px;
            vertical-align: middle;
            color: $white;
            background: $white;
            font-weight: normal;
            vertical-align: middle;
            box-sizing: initial;
            border: 5px solid $white;
            @include border-radius(50px);

            span {
                width: 40px;
                height: 40px;
                padding-top: .46em;
                display: block;
                background: $blue_light;
                border: 5px solid $white;
                text-align: center;
                font-size: 12px;
                @include border-radius(50%);
            }

            &:after {
                content: "";
                position: absolute;
                z-index: -1;
                top: 0;
                margin-top: 21px;
                height: 5px;
                display: block;
                width: 100%;
                left: 20px;
                right: 0px;
                @include border-radius(50px);
                background: $gray-300;

            }
        }

        &:last-child {
            width: 50px;

            .step_ball:after {
                display: none;
            }

        }

        .step_txt {
            width: 10em;
            text-align: center;
            position: absolute;
            display: block;
            margin-top: 10px;
            margin-left: -2.9em;
            color: $primary;
            font-size: 14px;

            span {
                display: block;
                color: $primary;
            }
        }


        &.active {
            .step_ball {
                background: $primary;

                span {
                    background: $primary;
                    border: 5px solid $white;
                }

            }
        }

        &.stepok {
            .step_ball {
                background: $white;

                span {
                    background: $primary;
                    border: 5px solid $white;
                }

            }

            .step_ball:before,
            .step_ball:after {
                background: $primary;
            }
        }
    }

    //---------------
    &.stepLess {//sm
        width: 70%;
        margin-left:auto;
        margin-right: auto;
        &>li {
            width: 33.33333%;

            &:last-child {
                width: 50px;
            }

            .step_ball {
                &:after {
                    left:25px;
                    width: 115%;
                }

            }

        }


    }

    @include media-breakpoint-up(md) {
        width: 70%;
        margin: 0 auto;

        &>li {
            .step_ball {
                &:after {
                    margin-top: 23px;
                    width: 75%;
                    left: 55px;
                }

            }
        }

        &.stepLess {
            width: 60%;

            &>li {
                .step_ball {
                    &:after {
                        left: 55px;
                        width: 100%;
                    }

                }

            }

        }
    }
}