body,
html {
    width: 100%;
    height: 100%;
    padding: 0px;
    margin: 0px;
    padding-right: 0 !important;
}

body {
    font-family: $font-family-base;
    font-size: 14px;
    letter-spacing: 0.020rem;
}
*{
    outline: none;
}
*.modal-open {
    overflow: scroll;
    padding-right: 0 !important;
}

small {
    margin-left: .2rem;
}

h2 {
    small {
        font-size: 1rem*.8;

        @include media-breakpoint-down(sm) {
            display: block;
            margin-top: 1rem;
        }
    }
}

p{
    font-size: 16px;
}
label,
.col-form-label,
    {
    font-size: 18px;
    color: $gray-700;
}

.custom-control-label.txt_sm {
    font-size: 15px;
}

.date {
    color: $gray-600;
    margin-left: 0;
    margin-bottom: 20px;
    display: block;
    text-align: right;
}

select,
.input_select {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background: $gray-300 url('../images/select_arrow.svg') no-repeat right .5em center;

    &:disabled {
        color: $gray-500;
        -webkit-filter: grayscale(100%);
        /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);
    }
}

table.noline {
    border: 0;

    tr,
    td,th {

        border: 0;
    }
}

.icon {
    height: 2.8em;
    display: inline-block;
    position: relative;
    margin-left: -3.2em;
    margin-right: .3em;

    img {
        height: 100%;
        width: auto;
        vertical-align: middle;
    }
}
.icon_main{
    height: 1em;
    margin-right: .35em;
    margin-left: -1.83em;
    img{
        height: 1.5em;
        margin-top: -.2em;
    }
}

.btn_inline {
    @include media-breakpoint-only(xs) {
        margin-top: 10px;
    }
}

.input_sm {
    @include media-breakpoint-up(md) {
        width: 47%;
    }
}

.input_md {
    @include media-breakpoint-up(md) {
        width: 70%;
    }
}

.input_file {
    // &:after{
    //     content: '';
    //     position: absolute;
    //     top:0;
    //     width: 100%;
    //     height: 33px;
    //     background: $gray-300;
    // }
    display: flex;
    align-items: flex-start;

    input,
    .fileUpload {
        height: 34px;
    }

    .fileUpload {
        width: 8em;
        margin-left: 10px;
    }
}

.fileUpload {
    position: relative;
    overflow: hidden;
    // margin: 10px;
}

.fileUpload input.upload {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    // font-size: 20px;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=.5);
}

.info_title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 1.5rem;
    color: $primary;

    @include media-breakpoint-up(lg) {
        font-size: 30px;
    }

    @include media-breakpoint-only(md) {
        font-size: 3vw;
    }
}
.page_title{
    font-weight: bold;
    font-size: 30px;
}

// .info_title_lg {
//     font-size: 30px;
//     text-align: center;
//     margin-bottom: .5rem;
//     margin-top: -.5rem;

//     @include media-breakpoint-only(md) {
//         text-align: left;
//         margin-left: 1.7rem;
//     }

//     @include media-breakpoint-up(lg) {
//         font-size: 40px; // font-size: 60px;
//         text-align: left;
//     }
// }

//for paper page (ex:privacy、trems)!!!!
.info_title_xl {
    font-size: 30px;
    margin-top: 1.5em;
    margin-bottom: 1em;
    font-weight: bold;
    @include media-breakpoint-only(md) {
        margin-top:2em;
        margin-bottom: 1em;

    }
    @include media-breakpoint-up(lg) {
        margin-top:.0;
        margin-bottom: 2em;
    }
}

.info_title_md {
    color: $primary-dark;
    font-weight: bold;
    font-size: 20px;
}

.info_title_sm {
    color: $primary;
    font-size: 18px;
}

.tit_md {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 2.2rem;

    &.text-primary-dark {
        font-size: 22px;
        text-align: center;

        @include media-breakpoint-only(md) {
            text-align: left;
            margin-left: 1.7rem;
        }

        @include media-breakpoint-up(lg) {
            text-align: left;
        }
    }
}

//for register page
.txt_note {
    margin-top: .5em;
    color: $gray-700;
    font-size: 12px;
    margin-left: 1em;
    margin-bottom: 0;

    &:before {
        content: "＊";
        margin-left: -1.3em;
    }

}

// for all page
h1.tit_top {}

.tit_main {
    font-size: 25px;
    text-align: center;
    font-weight: bold;

    @include media-breakpoint-up(md) {
        text-align: left;
        font-size: 30px;
    }
}

.txt_main {
    // font-size: 1;
    text-align: center;

    @include media-breakpoint-up(md) {
        text-align: left;

        font-size: 16px;
    }
}

ul.list_main {
    padding-left: 1.3em;
    li {
        list-style: none;
        margin-left: 0;
        padding-left: 0;

        &:before {
            content: " \2022 ";
            font-weight: bold;
            display: inline-block;
            width: 1em;
            margin-left: -1.2em;
        }
    }

    &.list_blue {
        li{
            font-size: 16px;
        }
        li:before {
            color: $primary;
        }

    }
    &.list_num {
        padding-left: 0;
        margin-left: 0;
        li{
            list-style: none;
            font-size: 16px;
            &:before {
                display: none;
            }
            span{
                color: $primary;
                font-weight: bold;
                font-size: 1.5em;
                margin-right: .3em;
            }
        }

    }

    &.list_purple {
        li:before {
            color: $purple;
        }

    }
}

.infoBox {
    margin-bottom: 2vw;
    h2.info_title{
        font-weight: normal;
    }

    .info_img {
        margin-bottom: 5vw;
    }

    .infoItem {
        margin-bottom: 4vw; // padding: 0 2vw;

        a {
            display: block;
            width: 100%;
            text-align: center;

            img {
                width: 100%;
            }
        }

        .txt_info {
            font-size: 13px;
        }

        .tit_md {
            text-align: center;
            padding-bottom: 1vw;
        }

        @include media-breakpoint-only(md) {
            padding: 0 30px;

            img {
                width: 80%;
            }
        }
    }

    &.infoBox_col_3 {
        .infoItem {
            padding: 0 20px;

            img {
                width: 65%;
            }

            @include media-breakpoint-only(md) {
                img {
                    width: 100%;
                }
            }
        }
    }
}

.articleBox {
    p {
        margin-bottom: 25px;
    }

    ol,
    ul {
        margin-bottom: 25px;
        padding-left: 1.3em;
        margin-left: 0;
        li{
            font-size: 16px;

        }
    }

    li {
        margin-bottom: .8rem;
    }

    ol.list_num {
        list-style-type: none;
        padding-left: 0;

        li {
            position: relative;
            // margin-left: .5em;
            margin-left: 0;

            span {
                position: absolute;
                left: -2em;
            }
        }
    }

    .info_title_sm {
        // margin-left: -.4em;
        margin-top: 1em;
        margin-bottom: .7em;
    }
}

.paymentBox {
    .info_img {
        margin-bottom: 0;

        @include media-breakpoint-up(lg) {
            align-self: center;
        }
    }
}

.list_dot_blue {
    list-style: none;

    li {
        position: relative;
        margin-bottom: 1em;

        &:before {
            content: "";
            width: 10px;
            height: 10px;
            background: $primary;
            display: inline-block;
            position: absolute;
            left: -1.2em;
            top: .5em;
            @include border-radius(10px);
        }
    }
}

input::-webkit-calendar-picker-indicator {
    display: none;
}

.btn {
    color: $white;
    font-size: 20px;
    width: 48%;
    padding-left: 0;
    padding-right: 0;
    display: inline-block;

    @include media-breakpoint-up(md) {
        width: 250px;
    }
}

.btn-outline-primary {
    color: $primary;

    &:hover {
        color: $primary;
        background: lighten($primary, 32%);
    }
}

.btn-sm {
    font-size: 15px;
}

.btn-lg {
    font-size: 13px;

    @include media-breakpoint-up(md) {
        font-size: 20px;
    }
}

.btn-rounded {
    //add
    @include border-radius($border-radius-rounded);
}
.btn-success{
    &:hover{
       filter: brightness(98%);
       color: $white;
    }
}
.form-control {
    font-size: 15px;
    border: 0;
}

select.form-control {
    padding: .32rem .75rem;
}

.form-inline {
    input {
        width: 90%;

        @include media-breakpoint-down(sm) {
            width: 80%;
        }
    }
}

.custom-control {

    input[type=checkbox],
    input[type=radio],
    .custom-control-label::before,
    .custom-control-label::after {
        font-size: 15px;
        top: 5px;
        width: 15px;
        height: 15px;
    }
}

.container {
    @include media-breakpoint-up(lg) {
        width: 960px;
        margin: 0 auto;

        &.con_contact {
            width: 1000px;

            .btn {
                float: left;
            }

            textarea {
                height: 8.7em;
                border: 1px solid #f00;
            }
        }
    }

    @include media-breakpoint-only(md) {
        &.con_contact {
            width: 60%;
            margin: 0 auto;

            .contact_info {
                width: 88%;
                margin: 0 auto;
            }
        }
    }

    @include media-breakpoint-down(sm) {
        &.con_contact {
            width: 100%;

            .contact_info {
                margin-left: 3vw;

                li {
                    font-size: 12px;
                }
            }
        }
    }
}

.custom-control-input:checked~.custom-control-label {
    &::before {
        //   @include gradient-bg($custom-control-indicator-checked-bg);
        background: $primary;
    }

    &::after {
        //   background-image: $custom-checkbox-indicator-icon-checked;
    }
}

