.modal-body {
    text-align: left;
    padding: 2.5rem;
    font-size: 15px;
    line-height: 1.8em;
}

.modal-header {
    width: 100%;
    text-align: center;
    position: relative;

    .modal-title {
        width: 100%;
    }

    button.close {
        position: absolute;
        top: 1rem;
        right: 1rem;
    }
}

.modal-footer {
    width: 100%;

    .btn {
        margin: .5rem auto;
    }

}

.custom_modal {
    .modal-content {
        z-index: 9999;

    }

    &:after {
        content: '';
        width: 100%;
        height: 100%;
        background: $black;
        opacity: .2;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9998;
    }
}

//for JQ
.arrow_down{
    @include tran(0.2s, ease);
    img{
        @include tran(0.3s, ease);
        @include rotate(90);
    }
    
}